@keyframes draw-user-bg {
  80% {
    stroke-dashoffset: 0;
    fill-opacity: 0;
    stroke: #e0e5ff; }
  100% {
    stroke-dashoffset: 0;
    fill-opacity: 1;
    stroke: #f9faff; } }

@keyframes fade-in {
